import { pathToBase64, base64ToPath } from '../../components/GRZX/js_sdk/gsq-image-tools/image-tools/index.js';
import {
	mapState,
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			textCode:"获取验证码",
			phoneNumber:'',
			captchaCode:'',
			imgHeight:'',
			loginType:'',
			urlData:'',
		}
	},
	onLoad(options) {
		this.urlData=options.urlData;
		this.load(options.loginType);
	},
	methods: {
		...mapMutations(['login']),
		async load(e){
			var that=this;
			uni.getSystemInfo({
			　　success: function(res) { // res - 各种参数
					that.imgHeight=res.windowHeight;
			       }
			});
			that.loginType=e;
			console.log(e)
		},
		judgeNum(val){  //只能输入数字
			var regPos = /^\d+(\.\d+)?$/; //非负浮点数
			    var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
			    if(regPos.test(val) || regNeg.test(val)) {
			        return true;
			    } else {
			        return false;
			    }
		},
		inputChange1(e){
			var num=e.detail.value;
			if(this.judgeNum(num)){
				
			}else{
				uni.showToast({
					title : '请输入正确的手机号码',
					icon : 'none',
				})
			}
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		inputChange2(e){
			var num=e.detail.value;
			if(this.judgeNum(num)){
				
			}else{
				uni.showToast({
					title : '请输入正确的验证码',
					icon : 'none',
				})
			}
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		loginClick(){	 //登录按钮
			uni.showLoading({
				title:'登录中...'
			})
			this.logining=true;
			var that=this;
			const {phoneNumber, captchaCode} = this;		
			var phone=this.phoneNumber;
			var captcha=this.captchaCode;
			if(phone==null||phone==""){
				uni.showToast({
					title:"请输入手机号码",
					icon:"none"
				})
			}else{
				if(captcha==null||captcha==""){
					uni.showToast({
						title:"请输入验证码",
						icon:"none"
					})
				}else{
					uni.getStorage({
						key:'captchaCode',
						success(res) {
							if(captcha==res.data.code&&phone==res.data.phone){
								uni.request({
									// url:'http://111.231.109.113:8002/api/person/login',
									url:that.$GrzxInter.Interface.login.value,
									data:{
										phoneNumber:phone,
									},
									method:that.$GrzxInter.Interface.login.method,
									success(res) {
										console.log(res)
										uni.removeStorage({
											key:'captchaCode',
										})
										uni.setStorage({
											key:'userInfo',
											data:res.data.data
										})
										uni.getStorage({
											key:'userInfo',
											success:function(user){
												//console.log(user,"user")
												if(user.data.nickname==""||user.data.nickname==null){
													user.data.nickname="用户"+user.data.phoneNumber;
												}
												var base64=res.data.data.portrait;
												if(that.isBase64(base64)){
													base64ToPath(base64)
													  .then(path => {
														user.data.portrait=path;
														that.login(user.data);
													  })
													  .catch(error => {
														console.error(error)
													  })	
												}else{
													that.login(user.data);
												}
												uni.hideLoading();
												uni.showToast({
													title:"登录成功!",
													icon:"none"
												})
												if(that.urlData==1){
													uni.switchTab({  //返回首页
														url:'/pages/Home/Index',
													}) 
												}else if(that.urlData==2){
													uni.switchTab({  //返回订单页
														url:'/pages/order/OrderList',
													}) 
												}else{
													console.log("返回上一页")
													uni.navigateBack();//返回上一页
												}
											}
										})
									}
								})
								
							}else{
								uni.showToast({
									title:"验证码错误",
									icon:"none"
								})
							}
						},
						fail(){
							uni.showToast({
								title:"验证码已过期，请重新获取",
								icon:"none"
							})	
						}
					})
				}
			}
		},
		wxLogin(){		//微信授权登录
			var theSelf=this;
			var getChina = require('../../components/GRZX/wfgo-getChina/getChina.js');
			//var address;
			uni.login({
				provider:'weixin',
				success:function(loginRes){
					uni.getUserInfo({	
						provider: 'weixin',
						success:function(res){			
							//address=getChina.pinyin(res.userInfo.province)+" "+getChina.pinyin(res.userInfo.city);
							address=res.userInfo.province+" "+res.userInfo.city;
							res.userInfo.address=address;
							res.userInfo.phoneNumber="";
							uni.setStorage({
								key:"userInfo",
								data:res.userInfo
							});
							theSelf.logining=true;
							uni.showToast({
								title: '授权成功',
								icon:"none"
							});
							if(res!=null||res!=""){
								theSelf.login(res.userInfo);						
							}
							//绑定手机号
							uni.getStorage({
								key:'userInfo',
								success:function(res1){
									if(res1.data.phoneNumber==""||res1.data.phoneNumber==null){
										uni.showToast({
											title : '请绑定手机号',
											icon : 'none',
										})
										setTimeout(function(){
											uni.navigateTo({	
												url  : '/pages/GRZX/wxLogin'
											}) 
										},500);
									}
								}
							})
							/* setTimeout(function(){
								uni.navigateBack({
									delta: 2
								})
							},1000); */	
						},
						fail:function(){
							theSelf.logining=false;
						}
					})
				}
			})
			
		},
		qqLogin(){		//QQ授权登录
			var theSelf=this;
			uni.getProvider({
			    service: 'oauth',
			    success: function (res) {
			        if (~res.provider.indexOf('qq')) {
			            uni.login({
			                provider: 'qq',
			                success: function (loginRes) {
			                    uni.getUserInfo({
			                    	provider: 'qq',
									success(logRes) {
										console.log(logRes,"logRes")
										var sex;
										if(logRes.userInfo.gender=="男"){
											sex=1;
										}
										if(logRes.userInfo.gender=="女"){
											sex=2;
										}
										var list={
											nickName:logRes.userInfo.nickname,
											openId:logRes.userInfo.openid,
											avatarUrl:logRes.userInfo.figureurl_qq_2,
											gender:sex,
										}
										console.log(list,"list")
										uni.setStorage({
											key:'userInfo',
											data:list
										})
										theSelf.logining=true;
										if(list!=null||list!=""){
											theSelf.login(list);						
										}
										uni.showToast({
											title: '授权成功',
											icon:"none"
										});
										uni.switchTab({
											url:'/pages/GRZX/user'
										})
										
									}
			                    })
			                }
			            });
			        }
			    }
			})
		},
		getCodeClick(e){	//获取验证码
			var self=this;
			const {phoneNumber, captchaCode} = this;		
			if(self.judgeNum(self.phoneNumber)){
				var timer=null,second=59; //倒计时的时间
				if(self.textCode == "获取验证码"){
				  self.textCode = second+"秒后重发";
				  if(self.textCode == "59秒后重发"){
					  timer=setInterval(function(){
					  second--;
					  if(second<=0){	
					  	self.textCode = "获取验证码";
					  	clearInterval(timer);
					  	second=59;	
					  }
					  else{			
					  	self.textCode = second+"秒后重发";
					  }},1000)
					  console.log(self.phoneNumber)
					 uni.request({
						// url:'http://218.67.107.93:9210/api/app/getLoginCode?phoneNumber='+self.phoneNumber,
						//url:'http://111.231.109.113:8002/api/person/getLoginCode',
						url:self.$GrzxInter.Interface.getLoginCode.value,
					    data:{
							phoneNumber:self.phoneNumber,
						},
						method:self.$GrzxInter.Interface.getLoginCode.method,
						success:(res)=>{
							console.log(res,"340");
					 		console.log(res.data.data);
							uni.setStorage({
								key:'captchaCode',
								data:{
									code:res.data.data,
									//code:'1234',
									phone:self.phoneNumber,
								}
							})
							uni.showToast({
								title:"验证码已发送，仅在5分钟内有效!",
								icon:"none"
							})
							setTimeout(function(){
								uni.removeStorage({
									key:'captchaCode',
								})
							},300000);
					    }
					 }) 
				  }
						
				}
			}else{
				uni.showToast({
					title : '请输入正确的手机号码',
					icon : 'none',
				})
			}
		},
		returnClick(){		//返回个人中心
			// uni.switchTab({
			// 	url:'/pages/GRZX/user'
			// })
			if(this.urlData==2){
				uni.switchTab({  //返回首页
					url:'/pages/Home/Index',
				}) 
			}else{
				uni.navigateBack();
			}
		},
		//------------判断是否为base64格式-----------
		isBase64:function(str) {
		    if (str ==='' || str.trim() ===''){ return false; }
		    try {
		        return btoa(atob(str)) == str;
		    } catch (err) {
		        return false;
		    }
		},
	}
}
